export default {
    name: 'app-reservation',
    props: {
        buttonColor: {
            type: String,
            default: function() {
                return "blue";
            }
        },
        buyBtnStyle: {
            type: Number,
            default: function() {
                return 4;
            }
        },
        buyBtnText: {
            type: String,
            default: function() {
                return "预约";
            }
        },
        customizeGoodsTag: {
            type: Boolean,
            default: function() {
                return false;
            }
        },
        fill: {
            type: Number,
            default: function() {
                return 0;
            }
        },
        goodsCoverProportion: {
            type: String,
            default: function() {
                return "3-2";
            }
        },
        goodsStyle: {
            type: Number,
            default: function() {
                return 3;
            }
        },
        goodsTagPicUrl: {
            type: String,
            default: function() {
                return "http://v4test2.zjhejiang.com/plugins/diy/assets/images/goods-tag-xp.png";
            }
        },
        list: {
            type: Array,
            default: function() {
                return [
                ];
            }
        },
        listStyle: {
            type: Number,
            default: function() {
                return 1;
            }
        },
        showBuyBtn: {
            type: Boolean,
            default: function() {
                return true;
            }
        },
        showGoodsName: {
            type: Boolean,
            default: function() {
                return false;
            }
        },
        showGoodsTag: {
            type: Boolean,
            default: function() {
                return false;
            }
        },
        scrollTop: {
            type: Number,
            default: function() {
                return 0;
            }
        },
        value: {
            type: Boolean,
            default: function() {
                return true;
            }
        }
    },
    data() {
        return {
            height: 0,
            request: this.value,
        }
    },
    created() {
        let that = this;
        wx.getSystemInfo({
            success: function(response) {
                that.height = response.windowHeight
            }
        })
    },
    methods: {
        autoEnd() {
            this.$lazyLoadingData('app-reservation').then(response => {
                if (response[0].height + response[0].top < this.height) {
                    this.request = false;
                    this.$emit('input', this.request);
                }
            });
        }
    },
    watch: {
        scrollTop: {
            handler: function(n,o) {
                if (n > o  &&  this.request) this.autoEnd();
            }
        }
    }
}
